@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
}

.backgroundContainer {
  background-size: cover;
  background-position: 50%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  min-width: 1200px;
  height: auto;
  min-height: 100%;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  /* bottom: -800px; */
  width: auto;
  min-width: 100%;
  height: auto;
  min-height: 100%;
  background: linear-gradient(rgb(59, 43, 68, 0.85) 0%, rgb(88, 75, 96) 50%);
  z-index: 1;
}

.Overlay {
  opacity: 0.7;
}

.ReactModal__Overlay {
  opacity: 0;
  /* transform: translateX(-100px); */
  transition: all 500ms ease-in-out;
  background-color: rgba(124, 107, 131, 0.75) !important;
}

.ReactModal__Overlay--after-open {
  opacity: 1;
  /* transform: translateX(0px); */
  background-color: rgba(124, 107, 131, 0.75) !important;
}

.ReactModal__Overlay--before-close {
  opacity: 0;
  /* transform: translateX(-100px); */
}

::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar,
::-webkit-scrollbar-track {
  background: rgba(59, 43, 68, 0.85);
}

.light-scrollbar::-webkit-scrollbar,
.light-scrollbar::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: rgba(227, 194, 254, 0.6);
  border-radius: 8px;
}

.tippy-box[data-theme~='custom'] {
  color: #26323d;
  box-shadow: 0 0 20px 4px rgba(154, 161, 177, 0.15),
    0 4px 80px -8px rgba(36, 40, 47, 0.25),
    0 4px 4px -2px rgba(91, 94, 105, 0.15);
  background-color: rgba(59, 43, 68);
}
.tippy-box[data-theme~='custom'][data-placement^='top'] > .tippy-arrow:before {
  border-top-color: rgba(59, 43, 68);
}
.tippy-box[data-theme~='custom'][data-placement^='bottom']
  > .tippy-arrow:before {
  border-bottom-color: rgba(59, 43, 68);
}
.tippy-box[data-theme~='custom'][data-placement^='left'] > .tippy-arrow:before {
  border-left-color: rgba(59, 43, 68);
}
.tippy-box[data-theme~='custom'][data-placement^='right']
  > .tippy-arrow:before {
  border-right-color: rgba(59, 43, 68);
}
.tippy-box[data-theme~='custom'] > .tippy-backdrop {
  background-color: rgba(59, 43, 68);
}
.tippy-box[data-theme~='custom'] > .tippy-svg-arrow {
  fill: rgba(59, 43, 68);
}

